<template>
	<div style="margin-top: 10px;">
		<!-- 主合约 -->
		<div class="all-w">
			<div class="owner-title">主合约</div>
			<!-- 设置静态收益率 -->
			<div class="owner-head">设置静态收益率</div>
			<el-input class="owner-input" v-model="masterInput1" placeholder="请输入日化率"></el-input>
			<div class="owner-tips">当前提示数据</div>
			<el-button class="owner-btn" type="primary" @click="masterClick1()">setStaticRate</el-button>
			<div class="owner-hr"></div>
			<!-- 设置动态加速层数收益率 -->
			<div class="owner-head">设置动态加速层数收益率</div>
			<el-input class="owner-input" v-model="masterInput2" placeholder="请输入要修改的层数"></el-input>
			<el-input class="owner-input" v-model="masterInput22" placeholder="请输入收益率"></el-input>
			<div class="owner-tips">当前提示数据</div>
			<el-button class="owner-btn" type="primary" @click="masterClick2()">setDynamicRate</el-button>
			<div class="owner-hr"></div>
			<!-- 激活保险池 -->
			<div class="owner-head">激活保险池</div>
			<div class="owner-tips">当前提示数据</div>
			<el-button class="owner-btn" type="primary" @click="masterClick3()">activeInsurance</el-button>
			<div class="owner-hr"></div>
			<!-- 划转永续池 -->
			<div class="owner-head">划转永续池</div>
			<el-input class="owner-input" v-model="masterInput4" placeholder="请输入接收地址"></el-input>
			<div class="owner-tips">当前提示数据</div>
			<el-button class="owner-btn" type="primary" @click="masterClick4()">activeSustainable</el-button>
			<div class="owner-hr"></div>
			<!-- 领取技术团队奖励 -->
			<div class="owner-head">领取技术团队奖励</div>
			<el-button class="owner-btn" type="primary" @click="masterClick5()">takeFounder</el-button>
			<div class="owner-hr"></div>
			<!-- 领取拓展运营费用 -->
			<div class="owner-head">领取拓展运营费用</div>
			<el-button class="owner-btn" type="primary" @click="masterClick6()">takeOperation</el-button>
			<div class="owner-hr"></div>
			<!-- 领取做市资金 -->
			<div class="owner-head">领取做市资金</div>
			<el-button class="owner-btn" type="primary" @click="masterClick7()">takeDex</el-button>
			<div class="owner-hr"></div>
		</div>
		
		<!-- 门票合约 -->
		<div class="all-w">
			<div class="owner-title">门票合约</div>
			<!-- 设置EID矩阵释放率 -->
			<div class="owner-head">设置EID矩阵释放率</div>
			<el-input v-model="ticketsInput1" placeholder="请输入日化率"></el-input>
			<div>当前提示数据</div>
			<el-button type="primary" @click="ticketsClick1()">setReleaseRate</el-button>
			<div>1</div>
		</div>
		
		<!-- 管理员方法 -->
		<div class="all-w">
			<div class="owner-title">更换管理员</div>
			<div>1</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				/* 主合约 */
				// 设置静态收益
				masterInput1: '',
				// 设置动态加速层数收益率
				masterInput2: '',
				masterInput22: '',
				// 划转永续池
				masterInput4: '',
				
				
				/* 门票合约 */
				ticketsInput1: '',
				/* 管理员方法 */
			}
		},
		
		created() {
			
		},
		
		methods: {
			/* 主合约 */
			// 设置静态收益
			async masterClick1() {
				console.log('点击了设置静态收益', this.masterInput1)
			},
			
			// 设置动态加速层数收益率
			async masterClick2() {
				console.log('设置动态加速层数收益率', this.masterInput2, this.masterInput22)
			},
			
			// 激活保险池
			async masterClick3() {
				console.log('激活保险池')
			},
			
			// 划转永续池
			async masterClick4() {
				console.log('划转永续池', this.masterInput4)
			},
			
			// 领取技术团队奖励
			async masterClick5() {
				console.log('领取技术团队奖励')
			},
			
			// 领取拓展运营费用
			async masterClick6() {
				console.log('领取拓展运营费用')
			},
			
			// 领取做市资金
			async masterClick7() {
				console.log('领取做市资金')
			},
			
			
			/* 门票合约 */
			async ticketsClick1() {
				
			},
			
		},
		
		
	}
</script>

<style>
	.owner-title {
		font-weight: bold;
		margin-bottom: 20px;
	}
	.owner-head {
		margin-bottom: 10px;
	}
	.owner-input {
		margin-bottom: 5px;
	}
	.owner-tips {
		margin-top: 5px;
		color: darkgrey;
	}
	.owner-btn {
		margin: 10px 0 10px 0;              
	}
	.owner-hr {
		background: #FFB400;
		width: 100%;
		height: 1px;
		margin: 0 auto;           
		margin-top: 10px;
		margin-bottom: 30px;
	}
	
	@media only screen and (min-width: 415px) {
		.owner-tips {
			font-size: 15px;
		}
	}
	
	/* 宽度尺寸小于414时 */
	@media only screen and (max-width: 414px) {
		.owner-tips {
			font-size: 13px;
		}
	}
	
</style>



